import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import './App.css';
import NL from './NL';
import EN from './EN';

class App extends Component {
    render() {
        const App = () => (
            <div>
                <Switch>
                    <Route exact path='/' component={EN}/>
                    <Route path='/nl' component={NL}/>
                    <Route path='/en' component={EN}/>
                </Switch>
            </div>
        )
        return (
            <Switch>
                <App/>
            </Switch>
        );
    }
}

export default App;