import React from 'react';
import Title from "./Title";
import boekImage from "../components/boek2.png";
import { Link } from 'react-router-dom';
import CookieBar from './CookieBar';

class Block1 extends React.Component {
    render() {
        return (
            <div className="blok1 marginmin15">
                <div className="row noMarginSides noPaddingSides">
                        <CookieBar/>

                    <div className="col">
                        <a href="#buydirect" className="buyButton">Buy now</a>
                    </div>
                    <div className="col">
                        <a href='./nl'>
                            <div href="/en" className="langButton"><strong>NL </strong>| EN</div>
                        </a>
                    </div>
                </div>
            <div className="row noMarginSides noPaddingSides">

                <div className="col">
                    <Title></Title>
                </div>
                <div className="col">
                    <img className="img-fluid boekImage" src={boekImage} alt="Error" style={{}} />
                </div>
            </div>
            </div>
        );
    }
}

export default Block1;