import React, { Component } from 'react';
import {Navbar, Nav, NavItem} from 'react-bootstrap'
import Block9 from './components/Block9'
import Block8 from './components/Block8';
import Block7 from './components/Block7';
import Block6Variatie from './components/Blok6Variatie';
import Block5Mobile from './components/Block5Mobile';
import Block5 from './components/Block5';
import Block4Mobile from './components/Block4Mobile';
import Block4 from './components/Block4';
import Block3 from './components/Block3';
import Block2Mobile from './components/Block2Mobile';
import Block2 from './components/Block2';
import Block1 from './components/Block1';
import './App.css';
import { Parallax, Background } from 'react-parallax';
import ScrollableAnchor, {configureAnchors} from 'react-scrollable-anchor'
import $ from 'jquery';
import Popper from 'popper.js';
import 'bootstrap/dist/js/bootstrap.bundle.min';
import { Link } from 'react-router-dom';
import boekImage from "./components/boek2.png";
import yellowArrow from "./components/yellowarrow.png";
import ReactGA from 'react-ga';


class NL extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isMobile: false
        };

        this.updatePredicate = this.updatePredicate.bind(this);
    }
    componentDidMount() {
        this.updatePredicate();
        window.addEventListener("resize", this.updatePredicate);
        document.title = "Alles Transactie";

        ReactGA.initialize('UA-130278066-1');
        ReactGA.pageview(window.location.pathname + window.location.search);
    }

    componentWillUnmount() {
        window.removeEventListener("resize", this.updatePredicate);
    }

    updatePredicate() {
        this.setState({ isMobile: window.innerWidth < 768 });
    }

    render() {
        const isMobile = this.state.isMobile;
        configureAnchors({keepLastAnchorHash: true, scrollUrlHashUpdate: false})

        return (
            <div className="App">

                <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css"
                      integrity="sha384-BVYiiSIFeK1dGmJRAkycuHAHRg32OmUcww7on3RYdg4Va+PmSTsz/K68vbdEjh4u"
                      crossOrigin="anonymous"/>

                    <link rel="stylesheet"
                          href="https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap-theme.min.css"
                          integrity="sha384-rHyoN1iRsVXV4nD0JutlnGaslCJuC7uwjduW9SVrLvRYooPp2bWYgmgJQIXwl/Sp"
                          crossOrigin="anonymous"/>

                <div className="main mainwrapper">
                    <div className="container-fluid noPaddingSides">
                        <div className="col"></div>
                        <div className="col">
                            <div className="mainCol">
                                <ScrollableAnchor id="title">
                                    <Block1/>
                                </ScrollableAnchor>
                                <div className="circle">
                                        <div className="circleText">Een <br/>boek van</div>
                                        <div className="circleTextSmall">Chiel Liezenberg <br/>Douwe Lycklama<br/>Shikko Nijland</div>
                                </div>
                                {isMobile ? (
                                    <ScrollableAnchor id="title2">
                                        <Block2Mobile/>
                                    </ScrollableAnchor>
                                ) : (
                                    <ScrollableAnchor id="title2">
                                        <Block2/>
                                    </ScrollableAnchor>
                                )}
                                <ScrollableAnchor id="information">
                                    <Block3/>
                                </ScrollableAnchor>
                                {isMobile ? (
                                    <ScrollableAnchor id="writers">
                                        <Block4Mobile/>
                                    </ScrollableAnchor>
                                ) : (
                                    <ScrollableAnchor id="writers">
                                        <Block4/>
                                    </ScrollableAnchor>
                                )}

                                {isMobile ? (
                                    <ScrollableAnchor id="interaction">
                                        <Block5Mobile/>
                                    </ScrollableAnchor>
                                ) : (
                                    <ScrollableAnchor id="interaction">
                                        <Block5/>
                                    </ScrollableAnchor>
                                )}
                                <ScrollableAnchor id="social">
                                    <Block6Variatie/>
                                </ScrollableAnchor>
                                <Block7/>
                                <ScrollableAnchor id="buydirect">
                                    <Block8/>
                                </ScrollableAnchor>
                                <Block9/>
                            </div>
                        </div>
                        <div className="col"></div>
                    </div>
                </div>
            </div>
    );
  }
}

export default NL;
